import React, { Component } from 'react';

export default class extends Component {
  constructor(props){
    super(props);
  }


  componentDidMount() {

  }


  render() {
    return (
        <div className="cadre">
          <div className="img"></div>
        <style jsx>{`
            .cadre {
              width: 225px;
              height: 318px;
              position: relative;
            }
            .img {
              width: 100%;
              height: 100%;
              background: url('${this.props.anime.image_url}')
            }
        `}</style>
        </div>
    );
  }
}
